<template>
  <div>
    <div class="title">
      热销推荐
    </div>  
    <ul>
      <router-link 
        tag="li"
        class="item" 
        v-for="item in travelList" 
        :key="item.id"
        to="/detail"
      >
        <img class="item-img" :src="item.img_src" alt="">
        <div class="item-info border-bottom">
          <p class="item-tit">{{item.title}}</p>
          <p class="item-desc">{{item.subtitle}}</p>
          <button class="item-button">查看详情</button>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
  export default{
    name:"HomeRecommend",
    props:{
      travelList:Array
    }
  }
</script>

<style lang="stylus" scoped>
  @import '~style/mixins.styl'
  .title
    margin-top:.2rem
    line-height:.8rem
    background:#eee
    text-indent:.2rem
  .item
    overflow:hidden
    display:flex
    height:1.9rem
    .item-img
      width:1.7rem
      height:1.7rem
      padding:.1rem
    .item-info
      flex:1
      padding:.1rem
      min-width:0
      .item-title
        line-height:.54rem
        font-size:.32rem
        ellipsis()
      .item-desc
        line-height:.4rem
        color:#ccc
        ellipsis()
      .item-button
        margin-top:.2rem
        color:#fff
        background:#ff9300
        padding:0 .2rem
        border-radius:.06rem
</style>


